import React, { Component } from 'react';
import { StyleSheet, Text, View } from 'react-native';


export default class Loading extends Component {
    constructor(props) {
        super(props)
    }
    render() {
        return(
        <View style = {styles.base}>
            <Text style = {styles.text}>Nalagam ...</Text>
            <Text>Samo nekaj sekund te loči od urnika ;)</Text>
        </View>
        );
    }
}
const styles = StyleSheet.create({
    base: {
        flex: 2,
        height: "100%",
        width: "100%",
        justifyContent:'center',
        alignItems: 'center'
    },
    text: {
        fontSize: 20,
        fontWeight: 'bold'
    }
})